<template>
  <view class="tab-switch">
    <view class="tab-container">
      <view class="tab-item" :class="{ active: currentTab === 'new' }" @click="switchTab('new')">
        <text>最新</text>
        <view v-if="currentTab === 'new'" class="active-bg"></view>
      </view>
      <view class="tab-item" :class="{ active2: currentTab === 'hot' }" @click="switchTab('hot')">
        <text>热门</text>
        <view v-if="currentTab === 'hot'" class="active-bg"></view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  name: 'TabSwitch',
  data() {
    return {
      currentTab: 'new', // 默认选中热门
    }
  },
  methods: {
    switchTab(tab) {
      this.currentTab = tab
      this.$emit('change', tab)
    },
  },
}
</script>

