      text {
        position: relative;
        z-index: 2;
        font-size: 28rpx;
        color: #fff;
      }

      &.active {
        border-radius: 100rpx 0 0 100rpx;
        color: #fff;
        background: $hubei-primary;

        &::after {
          content: '';
